@import "./normalize";
@import "./vars";
@import "./mixins";
@import "./fonts";
@import "./sprite_generated";

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
@import "../libs/swiper/swiper-bundle.min.css";
@import "../libs/splide/splide.min.css";

/*===== global styles =====*/
* {
  box-sizing: border-box;
}
// min-height: 0.001vw; // — Fixes Safari bug with viewport units in calc()

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1520px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;

  @include xs {
    padding: 0 20px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none !important;
}

button,
input {
  font-family: inherit;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.visualhidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(0 0 99.9% 99.9%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
}

// autoscroll block

.autoscroll__block {
  padding: 30px 0 31px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 4px solid #000;

  @include sm {
    border-bottom: 1px solid #d9d9d9;
  }

  @include xs {
    padding: 26px 0;
  }
}

.autoscroll__title {
  text-align: center;
  font-size: 140px;
  font-weight: 600;
  line-height: 122%;
  letter-spacing: 14px;
  text-transform: uppercase;

  @include xl {
    font-size: 102px;
    letter-spacing: 8px;
  }

  @include sm {
    font-size: 76px;
  }

  @include xs {
    font-size: 44px;
    line-height: normal;
    letter-spacing: 2.2px;
  }
}

// button 'showmore'

.showmore__btn {
  display: none;
  width: 100%;
  max-width: 400px;
  justify-content: center;
  align-items: center;
  margin: 48px auto 0;
  padding: 14px 30px;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  transition: all 0.2s ease-out;

  &:hover {
    color: #000;
    background-color: #fff;
  }

  @include xs {
    display: flex;
  }
}
