:root {
  --color_grey: #838383;
  --color__azure: #3896ab;
}

@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v25-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v25-cyrillic_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-600.ttf") format("truetype");
}
/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1520px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none !important;
}

button,
input {
  font-family: inherit;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.visualhidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(0 0 99.9% 99.9%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
}

.autoscroll__block {
  padding: 30px 0 31px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 4px solid #000;
}
@media (max-width: 991px) {
  .autoscroll__block {
    border-bottom: 1px solid #d9d9d9;
  }
}
@media (max-width: 767px) {
  .autoscroll__block {
    padding: 26px 0;
  }
}

.autoscroll__title {
  text-align: center;
  font-size: 140px;
  font-weight: 600;
  line-height: 122%;
  letter-spacing: 14px;
  text-transform: uppercase;
}
@media (max-width: 1600px) {
  .autoscroll__title {
    font-size: 102px;
    letter-spacing: 8px;
  }
}
@media (max-width: 991px) {
  .autoscroll__title {
    font-size: 76px;
  }
}
@media (max-width: 767px) {
  .autoscroll__title {
    font-size: 44px;
    line-height: normal;
    letter-spacing: 2.2px;
  }
}

.showmore__btn {
  display: none;
  width: 100%;
  max-width: 400px;
  justify-content: center;
  align-items: center;
  margin: 48px auto 0;
  padding: 14px 30px;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  color: #fff;
  background-color: #000;
  border: 1px solid #000;
  transition: all 0.2s ease-out;
}
.showmore__btn:hover {
  color: #000;
  background-color: #fff;
}
@media (max-width: 767px) {
  .showmore__btn {
    display: flex;
  }
}

.header {
  position: relative;
  z-index: 10;
  min-height: 46px;
  background-color: #000;
}

.header__container {
  position: relative;
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
}

.header__menu {
  position: relative;
  padding: 0 20px;
}
.header__menu ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0 20px;
  padding: 0 86px;
  overflow-x: auto;
}
.header__menu li {
  flex-shrink: 0;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
}
.header__menu a {
  display: inline-block;
  padding: 14px 0;
  transition: color 0.2s ease-out;
}
.header__menu a:hover {
  color: white;
}
@media (max-width: 1300px) {
  .header__menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }
  .header__menu ul {
    display: block;
    padding: 13px 69px;
    background-color: #000;
  }
  .header__menu li:not(:last-child) {
    margin-bottom: 28px;
  }
  .header__menu a {
    padding: 0;
  }
}

.header__btn {
  position: relative;
  display: none;
  justify-content: flex-start;
  align-items: center;
  gap: 0 17px;
  padding: 12px 20px 11px 27px;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  cursor: pointer;
}
.header__btn,
.header__btn svg,
.header__btn path {
  transition: all 0.2s ease-out;
}
.header__btn:hover {
  color: white;
}
.header__btn:hover path {
  fill: white;
}
.active .header__btn svg {
  transform: scaleY(-1);
}
@media (max-width: 1300px) {
  .header__btn {
    display: flex;
  }
}

.main {
  width: 100%;
  min-width: 320px;
  flex-grow: 1;
}

.panel {
  position: relative;
  z-index: 8;
  border-bottom: 1px solid #d9d9d9;
  background-color: #fff;
}

.panel__container {
  width: 100%;
  max-width: 1920px;
  min-height: 69px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 60px 0 54px;
  display: grid;
  grid-template-columns: 1fr 138px 1fr;
  align-items: center;
}
@media (max-width: 1300px) {
  .panel__container {
    padding: 0 20px;
  }
}
@media (max-width: 1000px) {
  .panel__container {
    min-height: 80px;
  }
}
.panel__container button,
.panel__container input,
.panel__container input::placeholder {
  color: #000;
  font-size: 16px;
  font-weight: 600;
}
.panel__container button,
.panel__container a {
  transition: all 0.2s ease-out;
}
.panel__container button:hover,
.panel__container a:hover {
  color: var(--color_grey, #838383);
}

.panel__nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 38px;
}

.panel__showCategories,
.panel__search {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 16px;
}

.panel__showCategories svg {
  display: block;
  width: 24px;
  height: 24px;
}
.panel__showCategories svg:nth-child(2) {
  display: none;
}
.panel__showCategories.active svg:nth-child(1) {
  display: none;
}
.panel__showCategories.active svg:nth-child(2) {
  display: block;
}
@media (max-width: 1000px) {
  .panel__showCategories svg {
    width: 28px;
    height: 28px;
  }
  .panel__showCategories span {
    display: none;
  }
}

.panel__search {
  position: relative;
  width: 26vw;
  max-width: 500px;
  height: 54px;
}
.panel__search img {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}
.panel__search input {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0 48px;
}
.panel__search input:focus, .panel__search input.active {
  background-color: #f1f1f1;
}
@media (max-width: 1300px) {
  .panel__search {
    width: 23vw;
  }
}
@media (max-width: 1000px) {
  .panel__search {
    display: none;
  }
}

.panel__clear {
  display: none;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  cursor: pointer;
}
.panel__clear.visible {
  display: block;
}

.panel__logo {
  position: relative;
  width: 138px;
}
.panel__logo img {
  display: block;
  width: 100%;
  height: auto;
}
@media (max-width: 1000px) {
  .panel__logo {
    width: 136px;
  }
}

.panel__menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.panel__user {
  display: flex;
  align-items: center;
  margin-right: 44px;
}
.panel__user:hover .user__icon {
  border-color: var(--color_grey, #838383);
}
.panel__user .panel__nonAuth {
  display: none;
}
.panel__user .panel__nonAuth svg {
  display: block;
  width: 24px;
  height: 24px;
}
.panel__user .panel__auth {
  display: flex;
}
.panel__user.not-authorized .panel__nonAuth {
  display: flex;
}
.panel__user.not-authorized .panel__auth {
  display: none;
}
@media (max-width: 1000px) {
  .panel__user {
    margin-right: 0;
  }
  .panel__user .user__name {
    display: none;
  }
  .panel__user .panel__nonAuth svg {
    width: 28px;
    height: 28px;
  }
}

.panel__nonAuth,
.panel__auth {
  align-items: center;
  gap: 0 16px;
}

.user__icon {
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d9d9d9;
  border: 2px solid #000;
  border-radius: 50%;
  text-transform: uppercase;
  transition: all 0.2s ease-out;
}

.panel__favorites {
  margin-right: 36px;
}
@media (max-width: 1000px) {
  .panel__favorites {
    display: none;
  }
}

@media (max-width: 1000px) {
  .panel__cart {
    display: none;
  }
}

.panel__favorites,
.panel__cart {
  position: relative;
}

.panel__showCategories path,
.panel__user path,
.panel__cart path,
.panel__favorites path {
  transition: all 0.2s ease-out;
}
.panel__showCategories:hover path,
.panel__user:hover path,
.panel__cart:hover path,
.panel__favorites:hover path {
  fill: var(--color_grey, #838383);
  stroke: var(--color_grey, #838383);
}

.panel__amout {
  display: none;
  position: absolute;
  bottom: 15px;
  left: 15px;
  z-index: 2;
  min-width: 24px;
  min-height: 24px;
  align-items: center;
  justify-content: center;
  padding: 3px;
  font-size: 14px;
  font-weight: 600;
  border-radius: 50%;
  color: white;
  background-color: var(--color__azure, #3896ab);
}
.panel__amout::after {
  content: "";
  display: block;
  padding-top: 100%;
}
.panel__amout span {
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
}
.non-empty .panel__amout {
  display: flex;
}

.menu {
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  width: 100%;
  z-index: 5;
  background-color: #fff;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-out;
}
.menu.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.menu__overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s ease-out;
  cursor: pointer;
}
.menu__overlay.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.menu__search {
  display: flex;
  justify-content: center;
}
.menu__search .panel__search {
  display: none;
}
@media (max-width: 1000px) {
  .menu__search {
    padding: 16px 20px;
    border-bottom: 1px solid #d9d9d9;
  }
  .menu__search .panel__search {
    display: block;
    width: 100%;
    max-width: 500px;
  }
  .menu__search .panel__search input {
    background-color: #f1f1f1;
  }
}

.menu__container {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 60px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.menu__container li {
  min-height: 80px;
  border-bottom: 1px solid #d9d9d9;
  border-right: 1px solid #d9d9d9;
}
.menu__container li:nth-child(5n+5) {
  border-right: none;
}
.menu__container li:last-child {
  border-right: none;
}
.menu__container li:hover {
  position: relative;
  z-index: 3;
  box-shadow: -1px 0px 0px 0px #d9d9d9;
}
.menu__container li:hover a {
  background-color: #d9d9d9;
}
.menu__container a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 24px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
}
.menu__container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 24px;
  pointer-events: none;
  background-color: #fff;
}
.menu__container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 24px;
  pointer-events: none;
  background-color: #fff;
}
@media (max-width: 1600px) {
  .menu__container {
    grid-template-columns: repeat(4, 1fr);
  }
  .menu__container li:nth-child(5n+5) {
    border-right: 1px solid #d9d9d9;
  }
  .menu__container li:nth-child(4n+4) {
    border-right: none;
  }
  .menu__container a {
    padding: 8px 12px;
  }
}
@media (max-width: 1300px) {
  .menu__container {
    grid-template-columns: repeat(3, 1fr);
  }
  .menu__container li:nth-child(4n+4) {
    border-right: 1px solid #d9d9d9;
  }
  .menu__container li:nth-child(3n+3) {
    border-right: none;
  }
}
@media (max-width: 1000px) {
  .menu__container {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 40px;
  }
  .menu__container li:nth-child(3n+3) {
    border-right: 1px solid #d9d9d9;
  }
  .menu__container li:nth-child(2n+2) {
    border-right: none;
  }
  .menu__container::before {
    display: none;
  }
}
@media (max-width: 767px) {
  .menu__container {
    grid-template-columns: 1fr;
    padding: 0 24px;
  }
  .menu__container li {
    border-right: none;
  }
  .menu__container li:nth-child(n) {
    border-right: none;
  }
}

.intro {
  max-height: 813px;
  overflow: hidden;
}

.intro__container {
  position: relative;
  margin: 0 auto;
}

.intro__wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(3, 75%);
  gap: 0 24px;
}
@media (max-width: 1300px) {
  .intro__wrapper {
    grid-template-columns: repeat(3, 100%);
    gap: 0;
  }
}

.intro__block {
  position: relative;
}
